export default {
  data() {
    return {
      authorizationPhone: false,
      properties: {
        // 这里定义了innerText属性，属性值可以在组件使用时指定 
        innerTitle: {
          type: String,
          value: '我的'
        },
        // 是否显示返回图标
        isShowBack: {
          type: String,
          value: 'true'
        }
      },
      innerTitle: {
        type: String,
        value: '我的'
      },
      isShowBack: {
        type: String,
        value: 'true'
      },
      // 用户信息
      user: [],
      // 我的订单
      order: [{
        img: "../../static/images/img/my/icon_03.png",
        title: "待付款",
        num: "1",
        url: "../payment/payment"
      }, {
        img: "../../static/images/img/my/icon_05.png",
        title: "待配送",
        num: "2"
      }, {
        img: "../../static/images/img/my/icon_07.png",
        title: "配送中",
        num: "20"
      }, {
        img: "../../static/images/img/my/icon_09.png",
        title: "售后",
        num: "100"
      }],
      cellPhone: ""
    };
  },

  components: {},
  props: {},

  /**
   * 生命周期函数--监听页面加载
   */
  onLoad: function (options) {
    var that = this;
    getApp().globalData.initPage(that);
    that.cxyhxx();

    if (that.userInfo == null) {
      that.userInfo = getApp().globalData.userInfo;

      if (that.userInfo.cellPhone != null && "" != that.userInfo.cellPhone) {
        that.setData({
          authorizationPhone: false
        });
        console.log("false" + that.authorizationPhone);
      } else {
        that.setData({
          authorizationPhone: true
        });
        console.log("ture" + that.authorizationPhone);
      }

      console.log(that.authorizationPhone);
    } //测试是否能拿到数据


    console.log(that.ces);
  },
  methods: {
    // 得到用户手机号码getPhonneNumber
    getPhoneNumber(e) {
      var that = this; //用户还没有授权  授权后才让进去  不授权不让！！！
      //检查用户是否处于登陆状态
      // wx.checkSession({
      //  success: function() {
      //还处于登陆状态   用户授权了

      var ency = e.detail.encryptedData;
      var iv = e.detail.iv;
      uni.request({
        url: getApp().globalData.url + '/member/getPhoneNumberByOpenId',
        data: {
          'openId': getApp().globalData.userInfo.openId,
          'ency': ency,
          'iv': iv
        },
        header: {
          "content-type": "application/x-www-form-urlencoded"
        },
        method: "post",
        success: res => {
          //调用成功 
          if (res == -1) {//没有带参数进来
          } else if (res == 0) {
            //系统没修改成功  给用户提示
            conssole.log("系统没修改成功  给用户提示");
          } else {
            //保存手机号码成功
            console.log("//保存手机号码成功");
            console.log(res.data);
            getApp().globalData.userInfo.cellPhone = res.data;
            that.setData({
              cellPhone: res.data,
              authorizationPhone: false
            }); //跳转到我的会员卡页面

            console.log("执行保存号码成功，跳转到我的会员卡页面");
            uni.navigateTo({
              url: '../vip/vip'
            });
          }
        },
        fail: res => {
          //调用接口失败
          console.log(res);
        }
      });
      that.cxyhxx(); //},
      //fail: function() {
      //没有处于登陆状态   执行登陆 
      //  console.log("处于没登陆状态  记得调用登陆方法")
      //   }
      //  }),
    },

    //跳转
    nav: function (e) {
      var zz = e.currentTarget.dataset.url;
      uni.navigateTo({
        url: zz
      });
    },
    // 会员登录
    dl: function (e) {
      uni.navigateTo({
        url: '../zc/index'
      });
    },
    dingdan: function (e) {
      
      uni.navigateTo({
        url:"../shopcart/myorder/forpayment/forpayment"
      });
    },
    cxyhxx: function () {
      var that = this;
      uni.request({
        url: getApp().globalData.url + '/member/getMemberByUserID',
        data: {
          userId: getApp().globalData.userInfo.userId
        },
        method: 'POST',
        header: {
          "Content-Type": "application/x-www-form-urlencoded"
        },
        success: res => {
          that.setData({
            user: res.data.data
          });
        }
      });
    },
    setData: function (obj, callback) {
      let that = this;
      let keys = [];
      let val, data;
      Object.keys(obj).forEach(function (key) {
        keys = key.split('.');
        val = obj[key];
        data = that.$data;
        keys.forEach(function (key2, index) {
          if (index + 1 == keys.length) {
            that.$set(data, key2, val);
          } else {
            if (!data[key2]) {
              that.$set(data, key2, {});
            }
          }

          data = data[key2];
        });
      });
      callback && callback();
    }
  }
};
